body {
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  .login-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  input[type=password] {
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    outline: none;
    text-align: center;
  }
  
  button[type=submit] {
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
  }
  
  @media (min-width: 768px) {
    /* Styles for tablets and larger screens */
    .login-container {
      height: unset;
    }
  }